import Base from "./Base";
export default class ServerMember extends Base {
    nickname;
    server;
    user;
    userId;
    constructor(client, data, guild) {
        super(client);
        this.nickname = data.nickname;
        this.server = guild;
        this.userId = data.user_id;
    }
    async init() {
        this.user = await this.client.users.fetch(this.userId);
        return this;
    }
}
