import ServerMember from "../structures/ServerMember";
import CacheManager from "./CacheManager";
export default class ServerMemberManager extends CacheManager {
    server;
    constructor(server) {
        super(server.client, ServerMember);
        this.server = server;
    }
    async fetch(id, force) {
        // Check if it is in the cache
        if (this.cache.has(id) && !force) {
            return this.cache.get(id);
        }
        const data = (await this.client.sendHTTP(`/guilds/${this.server.id}/members/${id}`, "get")).data;
        const instance = await new ServerMember(this.client, data, this.server).init();
        this.cache.set(id, instance);
        return instance;
    }
}
