export default class Base {
    client;
    constructor(client) {
        this.client = client;
    }
    /**
     * Removes everything from the resource, except values of type
     * boolean, number, null, string, undefined, etc.
     *
     * Useful for printing to the console.
     * @returns The stripped object
     */
    strip() {
        let values = {};
        for (const i in this) {
            if (["string", "number", "boolean"].includes(typeof this[i])
                || this[i] === null
                || this[i] === undefined) {
                values[i.toString()] = this[i];
            }
        }
        return values;
    }
}
