import InviteManager from "./InviteManager";
export default class ServerInviteManager extends InviteManager {
    server;
    constructor(client, server) {
        super(client);
        this.server = server;
    }
    async fetchAll() {
        let result = (await this.client.sendHTTP(`/guilds/${this.server.id}/invites`, "get")).data.invites;
        let invites = [];
        for (let i in result) {
            invites.push(await this.client.invites.addData(result[i]));
        }
        return invites;
    }
    async create(options) {
        let result = (await this.client.sendHTTP(`/guilds/${this.server.id}/invites`, "post", {
            max_uses: options?.maxUses,
            channel_id: options?.channelId,
            expires_in: options?.expiresIn
        })).data;
        return await this.client.invites.addData(result);
    }
}
